.itemCard{
    height: 400px;
    .item{
        display: flex;
        flex-wrap: wrap;
        margin: 0 8px 16px;
        width: 224px;
        height: 330px;
        border-left: 1px rgb(0 0 0 / 1%) solid;
        border-right: 1px rgb(0 0 0 / 1%) solid;
        border-bottom: 1px rgb(0 0 0 / 10%) solid;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
        position: relative;
        cursor: pointer;
        overflow: hidden;
        box-shadow: 0 0 0 0 transparent;
        transition: .2s box-shadow, height ease-out;
        &:hover{
            box-shadow: 0 7px 16px 0 rgb(0 0 0 / 20%), 0 1px 3px 0 rgb(0 0 0 / 10%);
            height: auto;
        }
        a{
            text-decoration: none;
            width: 100%;
            height: 100%;
            &:hover > .itemContent .itemTitle p{
                opacity: 1;
            }
            &:hover > svg{
                opacity: 1;
                visibility: visible;
            }
            .imgProduct{
                background-color: #fff;
                width: 100%;
                height: 224px;
                padding: 25px 15px;
                img{
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }
            .itemContent{
                width: 100%;
                height: auto;
                padding: 20px 16px;
                background-color: #fff;
                border-top: 1px rgb(0 0 0 / 8%) solid;
                .itemPrice{
                    line-height: 22px;
                    .priceSimbol{
                        color: #333;
                        font-size: 24px;
                        font-weight: 500;
                        margin-right: 0.2em;
                    }
                    .price{
                        color: #333;
                        font-size: 24px;
                        font-weight: 500;
                    }
                }
                .itemTitle{
                    span{
                        color: #00a650;
                        font-size: 14px;
                        font-weight: 700;
                    }
                    p{
                        color: #666;
                        font-size: 14px;
                        font-weight: 300;
                        line-height: 1.3em;
                        margin-top: 5px;
                        opacity: 0;
                    }
                }
            }
            svg{
                position: absolute;
                width: 18px;
                top: 25px;
                right: 25px;
                fill: #3483fa;
                opacity: 0;
                visibility: hidden;
                &:hover{
                    fill: #1259c3;
                }
            }
        }
    }
}

.itemSkeleton{
    height: 400px;
    .item{
        display: flex;
        flex-wrap: wrap;
        margin: 0 8px 16px;
        width: 224px;
        height: 330px;
        border-left: 1px rgb(0 0 0 / 1%) solid;
        border-right: 1px rgb(0 0 0 / 1%) solid;
        border-bottom: 1px rgb(0 0 0 / 10%) solid;
        border-bottom-left-radius: 5px;
        border-bottom-right-radius: 5px;
        position: relative;
        cursor: wait;
        overflow: hidden;
        animation: opacity 1.5s infinite;
        .imgProduct{
            background-color: #ddd;
            width: 100%;
            height: 224px;
            padding: 25px 15px;
        }
        .itemContent{
            width: 100%;
            height: 106px;
            padding: 20px 16px;
            background-color: #f3f3f3;
            border-top: 1px rgba(0, 0, 0, 0.08) solid;
            .itemPrice{
                background-color: #ddd;
                width: 60%;
                height: 25px;
                margin-bottom: 5px;
                border-radius: 3px;
            }
            .itemTitle{
                background-color: #ddd;
                width: 40%;
                height: 18px;
                border-radius: 3px;
            }
        }
    }
}
@keyframes opacity {
    0%{
        opacity: 1;
    }
    50%{
        opacity: .4;
    }
    100%{
        opacity: 1;
    }
}